.footer {
}

.footer__box {
  padding: 95px 80px;
  display: flex;
  justify-content: space-between;
}
.footer__subtitle-wrapper {
  width: 477px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.footer__subtitle {
  font-weight: 400;
  font-size: 100px;
  line-height: 0.9;
  margin-bottom: 10px;
  color: var(--background-primary);
}
.footer__description {
  font-size: 18px;
  line-height: 1.6;
}
.footer__menu-text,
.footer__info-text {
  font-size: 30px;
  line-height: 1.2;
  text-transform: uppercase;
  color: var(--background-primary);
  margin-bottom: 50px;
}

.footer__menu-item,
.footer__info-item {
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 20px;
}
.footer__menu-link {
  color: inherit;
  transition: color 0.5s ease;
  text-decoration: none;
}
.footer__menu-item :hover,
.footer__menu-item :focus {
  color: var(--text-primary);
  transition: color 0.5s ease;
}
