.hero {
  position: relative;
}

.hero__title-wrapper {
  display: inline-flex;
  flex-direction: column;
  position: absolute;
  top: 77px;
  z-index: 99;
}
.hero__title {
  display: inline;
  margin-bottom: 11px;
  font-weight: 400;
  font-size: 200px;
  line-height: 0.9;
  color: var(--background-primary);
}
.hero__description {
  display: inline-block;
  padding-left: 180px;
  font-size: 60px;
  line-height: 1.1;
  text-transform: uppercase;
  color: var(--text-default);
}

.hero__description:last-child {
  padding-left: 340px;
}

.hero__image-wrapper {
  position: relative;
  margin-bottom: 90px;
  z-index: 99;
}
.hero__image {
  height: auto;
  margin-left: auto;
  width: 752px;
}
.hero__arrow {
  position: absolute;
  top: 842px;
  right: 212px;
  z-index: -1;
  display: flex;
  align-items: flex-end;
}

.hero__svg {
  width: 20px;
  height: 164px;
}
.hero__arrow-text {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 15px;
  margin-left: 20px;
}

.hero__button-box {
  display: flex;
  position: absolute;
  bottom: 90px;
  left: 50px;
  flex-direction: column;
}
.hero__button-image {
  width: 180px;
  margin-bottom: 10px;
}

.hero__button-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: var(--text-default);
}
.hero__button-wrapper {
  z-index: 99;
  position: absolute;
  top: 94px;
  left: 150px;
}
